.main {
  display: flex;
  flex: 1;
  height: 100%;
  flex-direction: column;
}
.bkImage {
  height: 332px;
}
.listView {
  flex-direction: row;
  display: flex;
  flex: 1;
  flex-wrap: wrap;
  padding: 10px;
  margin-top: -100px;
}
.listView div {
  display: flex;
  flex-direction: column;
  width: 45%;
}
.listItemView {
  margin-left: 5%;
  border-radius: 10px 10px 10px 10px;
  max-height: 220px;
}
.listItemView img {
  width: 100%;
  height: 104px;
  border-radius: 10px 10px 0px 0px;
}
.listItemView img + div {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 106px;
  width: 100%;
  padding: 10px;
  border-left-color: #e5e5e5;
  border-right-color: #e5e5e5;
  border-bottom-color: #e5e5e5;
  border-width: 1px;
  border-style: solid;
}
.listItemView img + div h4 {
  font-family: 'PingFang-SC-Medium';
  font-weight: 500;
}
.listItemFootButton {
  width: 80;
  height: 21px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #fda00f;
  box-shadow: 0px 0px 13px 2px rgba(253, 160, 15, 0.2);
  border-radius: 11px;
  color: white;
  font-size: 10px;
  margin-top: 20px;
}
.footProblemView {
  display: flex;
  flex-direction: column;
  padding: 0px 20px;
}
.footProblemView div {
  margin-top: 10px;
}
.footView {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  height: 70px;
  justify-content: center;
  align-items: center;
}
